html,
body,
.App,
#root {
  font-family: "Barlow", sans-serif;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #272727;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
}

.info {
  position: absolute;
  top: 100px;
  left: 100px;
  color: white;
  font-size: 28px;
}

.info ul {
  margin: 0;
  padding: 0;
}

.info ul li {
  display: inline;
  list-style-type: none;
}

.info a {
  color: white;
  text-decoration: none;
}

.info a:hover {
  text-decoration: underline;
}

.info ul li a:visited {
  color: white;
}
